"use client";

import React from "react";
import {
  EditorBlock,
  TextBlock,
  ImageBlock,
  VideoBlock,
  QuizBlock,
  // if you have this type in your types file:
  InteractiveBlock,
} from "@/lib/scorm/types";

import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { useLocale } from "@/hooks/use-locale";

/**
 * Child panels ‚Äì adjust paths/names if needed
 * (These are the smaller panels you already created)
 */
import  TextPanel from "@/components/scorm/panels/text-panel";
import  MediasPanel  from "@/components/scorm/panels/media-panel";
import  QuizPanel  from "@/components/scorm/panels/quiz-panel";
import  InteractivePanel  from "@/components/scorm/panels/interactive-panel";

interface PropertiesPanelProps {
  selectedBlock: EditorBlock | null;
  onBlockChange: (block: EditorBlock) => void;
}

export function PropertiesPanel({
  selectedBlock,
  onBlockChange,
}: PropertiesPanelProps) {
  const { t } = useLocale();

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ EMPTY STATE ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  if (!selectedBlock) {
    return (
      <div className="h-full flex flex-col items-center justify-center px-4 py-6 text-center">
        <div className="mb-3">
          <Badge
            variant="outline"
            className="text-[11px] px-2 py-0 rounded-full"
          >
            {t("scorm.props.title") || "Properties"}
          </Badge>
        </div>
        <p className="text-sm font-medium text-foreground mb-1">
          {t("scorm.props.emptyTitle") || "No block selected"}
        </p>
        <p className="text-[11px] text-muted-foreground max-w-xs">
          {t("scorm.props.desc") ||
            "Click any content block in the canvas to view and edit its properties here."}
        </p>
      </div>
    );
  }

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ TYPE LABELS (HEADER CHIP) ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  const typeLabelMap: Record<EditorBlock["type"], string> = {
    text: t("scorm.props.block.text.label") || "Text block",
    image: t("scorm.props.block.image.label") || "Image block",
    video: t("scorm.props.block.video.label") || "Video block",
    quiz: t("scorm.props.block.quiz.label") || "Quiz block",
    // if interactive exists in your union:
    // @ts-ignore
    interactive:
      t("scorm.props.block.interactive.label") || "Interactive block",
  };

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ BODY RENDER LOGIC ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  const renderBody = () => {
    switch (selectedBlock.type) {
      case "text":
        return (
          <TextPanel
            block={selectedBlock as TextBlock}
            onBlockChange={onBlockChange}
          />
        );

      case "image":
      case "video":
        // You can also split image & video if you prefer:
        return (
          <MediaPanel
            block={selectedBlock as ImageBlock | VideoBlock}
            onBlockChange={onBlockChange}
          />
        );

      case "quiz":
        return (
          <QuizPanel
            block={selectedBlock as QuizBlock}
            onBlockChange={onBlockChange}
          />
        );

      // @ts-ignore ‚Äì if you have InteractiveBlock type, remove this ignore
      case "interactive":
        return (
          <InteractivePropertiesPanel
            block={selectedBlock as InteractiveBlock}
            onBlockChange={onBlockChange}
          />
        );

      default:
        return (
          <p className="text-[11px] text-muted-foreground">
            {t("scorm.props.unsupported") ||
              "This block type does not expose editable properties yet."}
          </p>
        );
    }
  };

  // ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ MAIN PANEL SHELL ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
  return (
    <div className="h-full flex flex-col bg-card/80">
      {/* Header */}
      <div className="px-4 pt-4 pb-3">
        <div className="flex items-center justify-between gap-2">
          <div className="space-y-1">
            <h3 className="text-sm font-semibold leading-none">
              {t("scorm.props.title") || "Block properties"}
            </h3>
            <p className="text-[11px] text-muted-foreground">
              {t("scorm.props.subtitle") ||
                "Fine-tune the selected element to match your learning design."}
            </p>
          </div>
          <Badge
            variant="secondary"
            className="text-[10px] px-2 py-0 rounded-full"
          >
            {typeLabelMap[selectedBlock.type] ?? selectedBlock.type}
          </Badge>
        </div>
      </div>

      <Separator className="mb-3" />

      {/* Scrollable content */}
      <div className="flex-1 overflow-y-auto px-4 pb-4 space-y-4 text-xs">
        {renderBody()}
      </div>
    </div>
  );
}
